/*------ WATCH TOGGLE CSS ---------*/
.switch {
  position: relative;
  display: inline-block;
  width: 100%;
  height: 28px; }

.switch input {
  display: none; }

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #C0C0C0;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px; }

.slider:before {
  position: absolute;
  content: "";
  height: 23px;
  width: 23px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%; }

input:checked + .slider {
  background-color: #FF7C3B; }

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3; }

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(72px); }

.slider:after {
  content: 'WATCH';
  color: white;
  display: block;
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 55%;
  font-size: 11px;
  font-family: "Roboto Medium"; }

input:checked + .slider:after {
  content: 'WATCH';
  margin-left: -10px; }

