/* Set variables for all modes */
:root {
  /* Set the background of the entire app */
  /* Set the font family of the entire app */
  --ion-font-family: -apple-system, BlinkMacSystemFont, 'Helvetica Neue',
    'Roboto'; }

/* Set text color of the entire app for iOS only */
.ios {
  --ion-text-color: $secondary; }

/* Set text color of the entire app for Material Design only */
.md {
  --ion-text-color: $secondary; }

/* 

This is a  generic font used across the  app for all the text elements
Font : Helvetica Neue
Regular 


This font used exclusively for city names 
Font :Roboto Medium' 
medium 

*/
@font-face {
  font-family: 'Helvetica Neue';
  font-style: regular;
  font-weight: 100;
  src: local("Helvetica Neue "), local("Helvetica Neue"), url("/assets/fonts/HelveticaNeue.ttf") format("truetype"); }

@font-face {
  font-family: 'Roboto Medium';
  font-style: regular;
  font-weight: 100;
  src: local("Helvetica Neue "), local("Helvetica Neue"), url("/assets/fonts/Roboto-Medium.ttf") format("truetype"); }

