ion-button {
  background-color: #ff8447;
  background: filter-gradient(#ff8447, #ff6e25, vertical);
  background: background-image(linear-gradient(to bottom, #ff8447 0%, #ff6e25 100%)); }

ion-label {
  font-size: 13px; }

.tab_footer_label {
  font-size: 12px;
  margin-top: 1px; }

.tab_footer_label_manage {
  font-size: 12px;
  margin-top: 0; }

.tab_footer_label_more {
  font-size: 12px;
  margin-top: 5px; }

.tab-selected {
  color: #ff6418 !important; }

ion-tab-bar {
  height: 8.37vh;
  --background: linear-gradient(180deg, #ffffff, #f3f3f3);
  box-shadow: 0px 0px 27px #797979; }
  ion-tab-bar ion-tab-button {
    color: #000000 !important; }
  ion-tab-bar ion-icon {
    height: 25px;
    margin-bottom: 0;
    width: 37px; }
  ion-tab-bar .manage_icon {
    height: 28px; }
  ion-tab-bar .more_icon {
    height: 20px; }

.ion-card {
  margin: 0px 1px 10px 1px !important;
  border: 0.5px solid #dedede;
  box-shadow: 0px 3px 6px #3a4c8212;
  border-radius: 5px; }

ion-content {
  --background: #fcfcfc; }

.custom-padding {
  --padding-top: 1px !important;
  --padding-start: 10px !important;
  --padding-end: 10px !important; }

.arrow_forward {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  border: 1px solid #ff8a51;
  padding: 4px;
  border-width: 1.5px 1.5px 0 0; }

ion-back-button {
  --color: white !important;
  --icon-font-size: 30px !important;
  --icon-padding-start: 10px !important; }

.picker-opt-selected {
  color: black !important; }

/** responsive break points **/
/*
$grid-max-widths: (
 //lg:992px
); */
ion-header, ion-tab-bar, ion-list, ion-content,
ion-toolbar {
  margin: auto; }

@media (min-width: 1200px) {
  ion-header, ion-tab-bar, ion-list, ion-list, ion-toolbar {
    margin: 0;
    padding-right: calc((100% - 1200px) / 2);
    padding-left: calc((100% - 1200px) / 2); }
  ion-tab-bar {
    position: absolute;
    margin: auto;
    width: -webkit-fill-available; } }

@media only screen and (min-width: 320px) {
  ion-tab-bar {
    margin: 0; } }

