/* Set variables for all modes */
:root {
  /* Set the background of the entire app */

  /* Set the font family of the entire app */
  --ion-font-family: -apple-system, BlinkMacSystemFont, 'Helvetica Neue',
    'Roboto';
}

/* Set text color of the entire app for iOS only */
.ios {
  --ion-text-color: $secondary;
}

/* Set text color of the entire app for Material Design only */
.md {
  --ion-text-color: $secondary;
}
// Font path is used to include ionicons,
// Helvetica Neue and Roboto fonts

@import 'typography.scss';

// @import "Helvetica";
$font-stack: 'Roboto Medium';
$font-roboto: 'Helvetica Neue';
/*
body {
  font: 100% $font-stack;
  color: color($colors, primary);
} */

ion-button {
  //background-color: $primary;
  background-color: rgba(255, 132, 71, 1);
  background: filter-gradient(#ff8447, #ff6e25, vertical);
  background: background-image(
    linear-gradient(
      to bottom,
      rgba(255, 132, 71, 1) 0%,
      rgba(255, 110, 37, 1) 100%
    )
  );
}

ion-label {
  font-size: 13px;
}

.tab_footer_label {
  font-size: 12px;
  margin-top: 1px;
}
.tab_footer_label_manage {
  font-size: 12px;
  margin-top: 0;
}
.tab_footer_label_more {
  font-size: 12px;
  margin-top: 5px;
}
.tab-selected {
  color: $primary !important;
}
ion-tab-bar {
  height: 8.37vh;
  --background: linear-gradient(180deg, #ffffff, #f3f3f3);
  box-shadow: 0px 0px 27px #797979;
  ion-tab-button{
    color: #000000 !important;
  }
  ion-icon {
    height: 25px;
    margin-bottom: 0;
    width: 37px;
  }
  .manage_icon {
    height: 28px;
  }
  .more_icon {
    height: 20px;
  }
}
.ion-card {
  margin: 0px 1px 10px 1px !important;
  border: 0.5px solid $alto;
  box-shadow: 0px 3px 6px $darkblue;
  border-radius: 5px;
}
ion-content {
  --background: #fcfcfc;
}
.custom-padding {
  --padding-top: 1px !important;
  --padding-start: 10px !important;
  --padding-end: 10px !important;
}
.arrow_forward {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  border: 1px solid #ff8a51;
  padding: 4px;
  border-width: 1.5px 1.5px 0 0;
}
ion-back-button {
  --color: white !important;
  --icon-font-size: 30px !important;
  --icon-padding-start: 10px !important;
}
.picker-opt-selected{
  color: black !important;
}