.page-header {
  width: 100%;
  height: 100px;
  background: linear-gradient(180deg, #FCA364, #FD7D4F); }
  @media only screen and (min-width: 1200px) {
    .page-header {
      height: 180px; } }
  .page-header ion-tab-bar {
    position: relative;
    width: 315px;
    height: 38px;
    border-radius: 25px;
    --background: #F4F5F8;
    --color: #000000;
    padding: 0px 5px 0px 5px;
    margin: auto;
    margin-top: 12px;
    border: 0.5px solid #FBA464; }
    @media only screen and (min-width: 320px) {
      .page-header ion-tab-bar {
        width: 275px; } }
  .page-header ion-tab-button, .page-header a {
    border-radius: 25px; }
  .page-header .tab-selected {
    --background: white;
    border-radius: 20px;
    height: 30px;
    color: #000000;
    box-shadow: 0px 2px 5px #00000038;
    margin-right: 1px; }
  .page-header .tab-selected:active {
    --background: #ffd8bc; }
