/* Set text color of the entire app for iOS only */
.ios {
  --ion-text-color: $secondary; }

/* Set text color of the entire app for Material Design only */
.md {
  --ion-text-color: $secondary; }

/* 

This is a  generic font used across the  app for all the text elements
Font : Helvetica Neue
